import React, { Component } from 'react';
import { Button, Tree, message, Modal, Input } from 'antd';
import styles from './index.module.less';
import { postAction, getAction } from '@/axios';
import {hasButtonPermission} from '@/util'

const { TreeNode } = Tree;
// const MyIcon = Icon.createFromIconfontCN({
//     scriptUrl:"//at.alicdn.com/t/font_1331187_ydzjg7bsu2.js"
// })

export default class LeftTree extends Component {
    constructor(props){
        super(props);
        this.state = {
            modalType:"add",
            folderName:"",
            showModal:false,//编辑/新增文件夹
            selectedKeys:[],//选择的行key
            selectedNodes:[],//选择的行数据
            treeData:[
                {
                    folderName:"全部文件",
                    dataSetFolderId:"000",
                    subDataSetFolders:[

                    ]
                }
            ]
        }
    }
    componentDidMount(){
        this.getFolderTree();
    }
    componentDidUpdate(preProps){
        if(preProps.OutSelectedFolder.key!==this.props.OutSelectedFolder.key){
            this.setState({
                selectedKeys:[this.props.OutSelectedFolder.key]
            })
        }
    }
    //获取文件夹tree---请求
    getFolderTree = () => {
        getAction("/ybt/data/dataset/folder/trees").then(res => {
            if(res.success){
                this.setState({
                    treeData:[
                        {
                            folderName:"全部文件",
                            dataSetFolderId:"000",
                            subDataSetFolders:res.obj || []
                        }
                    ]
                })
            }else{
                message.error(res.msg)
            }
        })
    }
    //点击新增文件夹
    handleAddFolder = () => {
        const firstLevelFolders = this.state.treeData[0].subDataSetFolders.map(item => item.dataSetFolderId);
        if(this.state.selectedNodes[0] && firstLevelFolders.indexOf(this.state.selectedNodes[0].key)===-1 && this.state.selectedNodes[0].key!=="000" ){
            message.warning("二级目录下不可新建文件夹");
            return false
        }
        this.setState({showModal:true,modalType:"add",folderName:""})
    }
    //点击修改按钮
    handleOpenEdit = () => {
        // console.log(this.state.selectedNodes[0]);
        if(this.state.selectedNodes.length > 0 && this.state.selectedNodes[0].key !=="000"){
            this.setState({showModal:true,modalType:"edit",folderName:this.state.selectedNodes[0].props.title})
        }else{
            message.warn("请先选择需要修改的文件");
        }
    }
    //文件夹名输入框
    handleChangeFolderName = (e) => {
        this.setState({folderName:e.target.value})
    }
    //增加/修改文件夹弹窗确认按钮
    handleOk = () => {
        if(this.state.folderName===""){
            message.warn("文件名不能为空")
        }else{
            this.addFolderRequest();
            this.setState({showModal:false})
        }
    }
    // 增加/修改文件夹----请求
    addFolderRequest = () => {
        // if(this.state.treeData[0].subDataSetFolders.length===0){
        //     message.error("获取文件夹列表出错，请刷新页面再次请求");
        //     return false
        // }
        if(this.state.modalType==="add"){//增加文件夹
            let parentFolderId = "";
            if(this.state.selectedNodes.length>0 && this.state.selectedNodes[0].key!=="000"){
                parentFolderId=this.state.selectedNodes[0].key;
            }
            const params = {
                folderName:this.state.folderName,
                parentFolderId
            }
            postAction("/ybt/data/dataset/folder/create",params).then(res => {
                if(res.success){
                    message.success("添加成功");
                    this.getFolderTree();
                }else{
                    message.error(res.obj)
                }
            })
        }else{//修改文件夹名称
            const dataSetFolderId = this.state.selectedNodes[0].key;
            const folderName = this.state.folderName;
            postAction(`/ybt/data/dataset/folder/updateName?dataSetFolderId=${dataSetFolderId}&folderName=${folderName}`).then(res => {
                if(res.success){
                    message.success("修改成功");
                    this.getFolderTree();
                }else{
                    message.error(res.obj)
                }
            })
        }
    }
    //击删除文件夹
    handleDelete = () => {
        if(this.state.selectedNodes.length > 0 && this.state.selectedNodes[0].key !=="000"){
            const folderInfo = this.state.selectedNodes[0];
            Modal.confirm({
                title:"确定删除该文件夹吗？",
                onOk:()=>{
                    this.deleteFolderRequest(folderInfo.key)
                }
            }) 
        }else{
            message.warn("请先选择需要修改的文件")
        }
    }
    //删除文件夹---请求
    deleteFolderRequest = (key) => {
        postAction(`/ybt/data/dataset/folder/delete?dataSetFolderId=${key}`).then(res => {
            if(res.success){
                message.success("删除成功");
                this.getFolderTree();
                this.setState({
                    selectedKeys:[],
                    selectedNodes:[],
                })
            }else{
                message.error(res.obj)
            }
        })
    }
    renderTree = (data=[]) => data.map(item => {
        if(item.subDataSetFolders && item.subDataSetFolders.length>0){
            return <TreeNode title={item.folderName} key={item.dataSetFolderId}>
                {this.renderTree(item.subDataSetFolders)}
            </TreeNode>
        }else{
            return <TreeNode title={item.folderName} key={item.dataSetFolderId}></TreeNode>
        }
    })
    onSelect = (selectedKeys, e) => {
        if(e.selected){
            this.setState({ selectedKeys,selectedNodes:e.selectedNodes});
            let selectedNode = {...e.selectedNodes[0]}
            if(selectedNode.key==="000"){
                selectedNode.key=""
            }
            this.props.changeSelectedFolder(selectedNode)
        }else{
            return false
        }
        
    }
    render() {
        return (
            <div className={styles.leftContainer}>
                {
                    hasButtonPermission('dataSet001') &&
                    <Button icon="plus" type="primary" onClick={this.handleAddFolder} size="small">新增文件夹</Button>

                }
                {
                    hasButtonPermission('dataSet002') &&
<Button style={{margin:"0px 5px"}} onClick={this.handleOpenEdit} disabled={this.state.selectedNodes.length > 0?false:true} size="small">修改</Button>
                }
                {
                    hasButtonPermission('dataSet003') && 
                    <Button onClick={this.handleDelete} disabled={this.state.selectedNodes.length > 0?false:true} size="small">删除</Button>

                }
                
                <div className={styles.leftTree}>
                    <Tree
                        onSelect={this.onSelect}
                        selectedKeys={this.state.selectedKeys}
                        defaultExpandedKeys={["000"]}
                    >
                        {this.renderTree(this.state.treeData)}
                    </Tree>
                </div>
                <Modal
                    visible={this.state.showModal}
                    title={this.state.modalType==="add"?"新增":"编辑"}
                    onOk={this.handleOk}
                    onCancel={()=>{this.setState({showModal:false})}}
                    width={400}
                >
                    <div>
                        文件名：
                        <Input style={{width:290}} placeholder="请输入文件名" value={this.state.folderName} onChange={this.handleChangeFolderName}/>
                    </div>
                </Modal>
            </div>
        )
    }
}
